import { mapState } from "vuex";

export default {
    data() {
        return {
            detail: {},
            setting: {},
            first: true,
        }
    },
    computed: {
        ...mapState({
            region: state => state.mallConfig.__wxapp_img.region,
            bonus: state => state.mallConfig.__wxapp_img.bonus,
        })
    },
    methods: {
        toUpdate() {
            uni.navigateTo({
                url: '/plugins/region/index/index?up=1'
            });
        },
        getList() {
            let that = this;
            that.$request({
                url: that.$api.region.status,
            }).then(response=>{
                if(response.code == 0) {
                    that.first = false;
                    that.$hideLoading();
                    that.detail = response.data.region;
                    if(that.detail.status != 1) {
                        uni.redirectTo({
                            url: '/plugins/region/index/index'
                        })
                    }
                    if(that.detail.level_up_log.status == 1) {
                        that.clearLevel();
                    }
                }else {
                    that.$hideLoading();
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
        clearLevel() {
            let that = this;
            that.$request({
                url: that.$api.region.clear_up,
                method: 'post'
            }).then(response=>{
            })
        },
        getSetting() {
            let that = this;
            that.$request({
                url: that.$api.region.setting,
            }).then(response=>{
                if(response.code == 0) {
                    that.getList();
                    that.setting = response.data;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
                uni.hideLoading();
            });
        },
    },
    onLoad(options) { this.$commonLoad.onload(options);
        let that = this;
        that.$showLoading({
            type: 'global',
            text: '加载中...'
        });
        that.getSetting();
    },
    onShow() {
        if(!this.first) {
            this.getList();
        }
    }
}
